<template>
  <v-card class="my-1" max-width="374">
    <v-img height="250" :src="imgUrl"> </v-img>
    <v-card-title class="card-title">{{ title }}</v-card-title>
    <v-card-text class="card-text min-height">
      {{ description }}
    </v-card-text>
    <div class="action d-flex justify-center">
      <v-btn color="mb-5 white--text" :to="link">
        {{linkText}}
      </v-btn>
    </div>
  </v-card>
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      required: true,
    },
    image: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: true,
    },
    link: {
      type: String,
      required: true,
    },
    linkText: {
      type: String,
      required: false,
      default: "READ MORE"
    },
  },
  computed: {
    imgUrl: function() {
      return require(`@/assets/${this.image}`);
    },
  },
};
</script>
<style lang="scss">
.card-title {
  color: #db204e;
}
.card-text {
  color: #111 !important;
}
.min-height {
  min-height: 180px;
}
.action .v-btn {
  background-color: #db204e !important;
  width: 80%;
  text-transform: uppercase;
}
.v-sheet.v-card {
  border-radius: 20px !important;
}
</style>
