import Vue from 'vue'
import VueRouter from 'vue-router'
import Map from '@/components/Map'
import Login from '@/components/Login'
import About from '@/components/About'
import NotFound from '@/components/404'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Map',
    meta: {
      requiresAuth: true
    },
    component: Map
  },
  {
    path: '/login',
    name: 'Login',
    meta: {
      requiresAuth: false
    },
    component: Login
  },
  {
    path: '/about',
    name: 'About',
    meta: {
      requiresAuth: true
    },
    component: About
  },
  {
    path: '/api/auth',
    name: 'auth',
    meta: {
      requiresAuth: false
    }
  },
  {
    path: '/404',
    name: '404',
    meta: {
      requiresAuth: false
    },
    component: NotFound
  },
  {
    path: '*',
    redirect: '/404'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
