<script>
import * as d3 from "d3";
import * as geoData from "./data.json";
import _ from "lodash";
export default {
  computed: {
    chainages() {
      return this.$store.getters["chainage/GET_CHAINAGE"];
    },
  },
  methods: {
    getID(chainage) {
      return "chainage-" + chainage.replaceAll("-", "m").replaceAll(".", "_");
    },
    getStatus(updatedAt) {
      //return value can be 'progress', 'overdue' or 'coming'
      let now = this.$moment(new Date());
      let update = this.$moment(updatedAt);
      let duration = this.$moment.duration(now.diff(update));
      let days = duration.asDays();
      //in 24 hours: 'progress', in 3 months: 'coming', more than 3 months 'overdue'
      if (days <= 1.0) return "In Progress";
      else if (days <= 90.0) return "Coming Up";
      else return "Overdue";
    },
    distance(x1, y1, x2, y2) {
      let a = Math.abs(x1 - x2);
      let b = Math.abs(y1 - y2);

      let c = Math.abs(Math.sqrt(a * a + b * b));
      return c;
    },
    getPositionBetween(coorData, pos, mid, item) {
      const start = d3.select("#" + coorData[item.line][pos - 1].selector);
      const end = d3.select("#" + coorData[item.line][pos].selector);
      if (!start.size() || !end.size()) return;
      //calculate offset
      let dx, dy;
      switch (item.side) {
        case "West":
          if (end.attr("type") == "1" || start.attr("type") == "1") {
            dx = -5;
            dy = -10;
          } else if (end.attr("type") == "2" || start.attr("type") == "2") {
            dx = 0;
            dy = -10;
          } else {
            dx = 10;
            dy = -4;
          }
          break;
        case "East":
          if (end.attr("type") == "1" || start.attr("type") == "1") {
            dx = 5;
            dy = 10;
          } else if (end.attr("type") == "2" || start.attr("type") == "2") {
            dx = 0;
            dy = 10;
          } else {
            dx = -10;
            dy = 4;
          }
          break;
        case "Single":
          dx = 0;
          dy = 0;
          break;
        default:
          break;
      }
      let x1, y1, x2, y2;
      //set x1, y1 value by type 'rect' or 'circle
      switch (start.node().nodeName) {
        case "rect":
          x1 = +start.attr("x") + dx;
          y1 = +start.attr("y") + dy;
          break;
        case "circle":
          x1 = +start.attr("cx") + dx;
          y1 = +start.attr("cy") + dy;
          break;
        default:
          x1 = 0;
          y1 = 0;
          break;
      }
      //set x2, y2 value by type 'rect' or 'circle
      switch (end.node().nodeName) {
        case "rect":
          x2 = +end.attr("x") + dx + 6;
          y2 = +end.attr("y") + dy + 6;
          break;
        case "circle":
          x2 = +end.attr("cx") + dx;
          y2 = +end.attr("cy") + dy;
          break;
        default:
          x2 = 0;
          y2 = 0;
          break;
      }
      const startObj = coorData[item.line][pos - 1];
      const endObj = coorData[item.line][pos];
      return this.getPosition(
        x1,
        y1,
        x2,
        y2,
        startObj.value,
        endObj.value,
        mid,
        item.line
      );
    },
    //x1,y1, x2,y2, start, end, mind
    getPosition(x1, y1, x2, y2, start, end, mid, line) {
      const c = this.distance(x1, y1, x2, y2);
      const c1 = c * ((mid - start) / (end - start));
      const a1 = (y2 - y1) * ((mid - start) / (end - start));
      const b1 = Math.abs(Math.sqrt(c1 * c1 - a1 * a1));
      switch (line) {
        case "MLPA":
        case "MLYAW":
          return {
            x: x1 + b1,
            y: y1 + a1,
          };
        case "MLME":
        case "MLBRE":
        case "MLWE":
          return {
            x: x1 - b1,
            y: y1 + a1,
          };
        default:
          break;
      }
    },
  },
  mounted: function() {
    const coorData = geoData.default;
    if (!this.chainages || !this.chainages.length) return;
    const vm = this;
    let tooltip = d3.select(".tooltip");
    this.chainages.forEach(function(item) {
      if (!item) return;
      if (coorData[item.line] === undefined) return;
      if (parseFloat(item.chainage_start) >= parseFloat(item.chainage_end))
        return;
      let pos = _.findIndex(coorData[item.line], function(o) {
        return o.value > parseFloat(item.chainage_start);
      });
      const start_pos = vm.getPositionBetween(
        coorData,
        pos,
        parseFloat(item.chainage_start),
        item
      );
      pos = _.findIndex(coorData[item.line], function(o) {
        return o.value > parseFloat(item.chainage_end);
      });
      const end_pos = vm.getPositionBetween(
        coorData,
        pos,
        parseFloat(item.chainage_end),
        item
      );

      let x1 = start_pos.x;
      let y1 = start_pos.y;
      let x2 = end_pos.x;
      let y2 = end_pos.y;
      //decide color
      let stroke;
      switch (vm.getStatus(item.updatedAt)) {
        case "In Progress":
          stroke = "#eaa260";
          break;
        case "Overdue":
          stroke = "#ff0d0d";
          break;
        case "Coming Up":
          stroke = "#91d67b";
          break;
        default:
          stroke = "black";
          break;
      }

      //add colored line to the map
      d3.select("g#progress")
        .append("line")
        .data([item])
        .attr("class", function(d) {
          console.log("--d", d);
        })
        .attr("x1", x1)
        .attr("y1", y1)
        .attr("x2", x2)
        .attr("y2", y2)
        .attr("stroke", stroke)
        .attr("stroke-width", "9")
        .attr("stroke-linecap", "round")
        .style("cursor", "pointer")
        .on("mouseover", function() {
          tooltip
            .select(".status")
            .html("<strong>Status: " + vm.getStatus(item.updatedAt));
          tooltip
            .select(".description")
            .html("<strong>Description: </strong>" + item.description);
          tooltip
            .select(".work-type")
            .html("<strong>Work Type: </strong>" + item.work_type);
          tooltip.select(".line").html("<strong>Line: </strong>" + item.line);
          tooltip.select(".side").html("<strong>Side: </strong>" + item.side);
          tooltip
            .select(".asset-id")
            .html("<strong>RTA Asset ID: </strong>" + item.asset_id);
          tooltip
            .select(".created-at")
            .html(
              "<strong>Created: </strong>" +
                vm.$moment(item.createdAt).format("lll")
            );
          tooltip
            .select(".updated-at")
            .html(
              "<strong>Updated: </strong>" +
                vm.$moment(item.updatedAt).format("lll")
            );
          let left, top;
          if (event.pageX + 400 > window.innerWidth) {
            left = event.pageX - 350;
            top = event.pageY - tooltip.node().getBoundingClientRect().height;
          } else {
            left = event.pageX;
            top = event.pageY - 28;
          }

          tooltip
            .transition()
            .duration(200)
            .style("opacity", 1)
            .style("display", "flex")
            .style("left", left + "px")
            .style("top", top + "px");
        })
        .on("mouseout", function() {
          tooltip
            .transition()
            .duration(200)
            .style("opacity", 0.0);
        });
    });
    // d3.select("#chainage-3_0").style("fill", function() {
    //   return "red";
    // });
  },
  created: function() {
    this.$store.dispatch("chainage/FETCH_CHAINAGE");
  },
};
</script>
