import Vue from 'vue'
import App from './App.vue'
import router from './plugins/router'
import store from './plugins/vuex'
import vuetify from './plugins/vuetify'
import moment from './plugins/moment';
import axios from './plugins/axios';
import authService from './services/auth';

Vue.config.productionTip = false

router.beforeEach((to,from,next) => {
  /*eslint-disable*/
  if (to.matched.some(route => route.meta.requiresAuth &&  !localStorage.getItem('userInfo'))) {
    // this route requires auth, check if logged in
    // if not, redirect to login page.
    next({
      path: '/login',
    })
  }
  else if(to.matched.some(route => !route.meta.requiresAuth && localStorage.getItem('userInfo'))){
    // if user is logged in and an access token exists redirect to the home page because a logged in user shouldn't be able to acess a login page
    next({
      path: '/',
    })
  }
  else{
    /*eslint-disable*/
    next()
  }
})
Vue.prototype.$base_url = location.origin
Vue.prototype.$authService = authService
new Vue({
  router,
  store,
  vuetify,
  moment,
  axios,
  render: h => h(App)
}).$mount('#app')
