<template>
  <div>
    <v-navigation-drawer v-model="drawer" :mini-variant.sync="mini" permanent>
      <v-list-item class="px-2">
        <v-list-item-avatar>
          <!-- <v-img src="https://randomuser.me/api/portraits/men/85.jpg"></v-img> -->
          <v-icon dark class="avatar">
            mdi-account-circle
          </v-icon>
        </v-list-item-avatar>

        <v-list-item-title>{{ userName }}</v-list-item-title>

        <v-btn icon @click.stop="mini = !mini">
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
      </v-list-item>

      <v-divider></v-divider>

      <v-list dense>
        <v-list-item
          v-for="item in items"
          :key="item.title"
          @click.prevent="action(item.link)"
          active-class="highlighted"
          :class="item.path === currentPath ? 'highlighted' : ''"
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <v-img
        v-if="!mini"
        max-width="100px"
        class="logo"
        src="../assets/logo.png"
      ></v-img>
    </v-navigation-drawer>
    <div class="v-sidebar" :class="mini ? 'v-sidebar--small' : ''"></div>
  </div>
</template>

<script>
export default {
  name: "Sidebar",
  components: {
    // logo
  },
  methods: {
    action(to){
      if(to === '/logout')
        this.logout();
      else
        this.$router.push(to);
    },
    logout() {
      if(localStorage.getItem("userInfo"))
        localStorage.removeItem("userInfo");
      this.$authService.logout();
      this.$router.push("/");
    },
  },
  computed: {
    currentPath() {
      return this.$router.path;
    },
    userName(){
      return this.$store.getters['auth/GET_USER_INFO']['name'] || '';
    }
  },
  data() {
    return {
      drawer: true,
      items: [
        { title: "Dashboard", icon: "mdi-view-dashboard", link: "/" },
        { title: "About", icon: "mdi-information-variant", link: "/about" },
        { title: "Logout", icon: "mdi-logout", link: "/logout" },
      ],
      mini: false,
    };
  }
};
</script>
