<template>
  <v-app>
    <v-main>
      <Sidebar v-if="!hideSidebar" />

      <div class="content-wrapper">
        <Header />
        <div class="content">
          <transition
            mode="out-in"
            enter-active-class="animated fadeIn"
            leave-active-class="animated fadeOut"
            :duration="{ enter: 300, leave: 0 }"
          >
            <router-view />
          </transition>
        </div>
      </div>
    </v-main>
  </v-app>
</template>

<script>
import Sidebar from "./components/Sidebar";
import Header from "./components/Header";
export default {
  name: "App",
  components: {
    Sidebar,
    Header,
  },
  data: () => ({}),
  computed: {
    hideSidebar() {
      return (
        this.$route.name.toLowerCase() === "Login".toLowerCase() ||
        this.$route.name.toLowerCase() === "404".toLowerCase()
      );
    },
  },
};
</script>
