<template>
  <div class="login">
    <v-overlay :value="true" color="#000" opacity="0.55">
      <v-img max-width="150px" class="logo" src="../assets/logo.png"></v-img>
      <v-btn elevation="1" class="mt-5" color="primary" @click="login">
        <Microsoft class="mr-2" />
        continue with Microsoft
      </v-btn>
    </v-overlay>
  </div>
</template>
<script>
import Microsoft from "@/components/Microsoft";
export default {
  methods: {
    login() {
      // window.location.href=`${this.$base_url}/api/auth`
      this.loginFailed = false;
      this.$authService.login().then(
        (user) => {
          if (user) {
            this.$store.dispatch("auth/FETCH_USER_INFO", user);
            this.$router.push("/");
          } else {
            if (localStorage.getItem("localStorage"))
              localStorage.removeItem("userInfo");
          }
        },
        () => {
          if (localStorage.getItem("localStorage"))
            localStorage.removeItem("userInfo");
        }
      );
    },
  },
  components: {
    Microsoft,
  },
  created() {},
};
</script>
<style lang="scss">
.login {
  height: calc(100vh - 64px);
  background: url("../assets/background.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;

  .v-overlay {
    height: calc(100vh - 64px);
    top: 64px;
  }
}
.v-overlay__content {
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>
