import Vue from 'vue'

export default {
    namespaced: true,
    state: {
        chainage: JSON.parse(localStorage.getItem('chainage'))
    },
    getters: {
        GET_CHAINAGE: function (state) {
            return state.chainage;
        }
    },
    mutations: {
        SET_CHAINAGE: function (state, payload) {
            let data;
            if(payload === undefined)
                data = {};
            else
                data = payload
            localStorage.setItem('chainage', JSON.stringify(data));
            state.chainage = data;
        }
    },
    actions: {
        FETCH_CHAINAGE: function (context) {

            Vue.axios.get('/api/chainages')
                .then(function (response) {
                    context.commit('SET_CHAINAGE', response.data.data)
                })
                .catch(function () { });
        }
    }
}